dialog {
  animation: .7s ease-out fade-out;
}

dialog[open] {
  animation: .7s ease-out fade-in;
}

dialog[open]::-ms-backdrop {
  animation: .7s ease-out forwards backdrop-fade-in;
}

dialog[open]::backdrop {
  animation: .7s ease-out forwards backdrop-fade-in;
}

@keyframes fade-in {
  0% {
    opacity: 0;
    display: none;
    transform: scaleY(0);
  }

  100% {
    opacity: 1;
    display: block;
    transform: scaleY(1);
  }
}

@keyframes fade-out {
  0% {
    opacity: 1;
    display: block;
    transform: scaleY(1);
  }

  100% {
    opacity: 0;
    display: none;
    transform: scaleY(0);
  }
}

@keyframes backdrop-fade-in {
  0% {
    background-color: rgba(0, 0, 0, 0);
  }

  100% {
    background-color: rgba(0, 0, 0, .25);
  }
}

